.App {
  text-align: center;
  font-family: Arial;
  background: url('./Green-Textures-Wallpapers.jpg');
}

.filter-list {

}

.header {
  background: url('./curb.jpg');
  margin-top: 0px;
  margin-bottom: 0px;
  padding: 30px;

}

h1 {
  color: white;
  text-shadow: 1px 1px 10px #FF0000;
  font-size: 4em;
  text-align: center;
  font-weight: regular;
  font-family: "Lucida Grande"
}

h2 {
  color: white;
  font-style: bold;
  text-shadow: 2px 2px 10px black;
}

img {
  -ms-flex-item-align: middle;
      align-self: middle;
  width: 100%;
  height: 100%;

}
.list {
    list-style-type: none;

}

.dropDown {
  font-size: 0.7em;
  margin-top: 3px;
}


.item-box {
    width: 80vw;
    height: 30vw;
    border: 3px solid green;
    padding: 5px;
    margin: 25px;
    background-color: white;

}

.item-pic {
  width: 25vw;
  height: 25vw;
  float:left;
  margin: 10px;
}

.parameters {
  text-align: middle;
  border-top: 3px solid black;
  border-bottom: 3px solid grey;
  font-size: 1.8em;
  font-family: "Lucida Grande";
  background-color: #000;
  height: 60px;

}

.parameterseletions {
  height: 10vw;

}

.label{
  font-size: 12pt;
  margin: 5px;
}

.filterBox {
  float: left;
  padding-left: 2%;

}

.searchInput {
width:30vw;
height: 36px;
margin: 10px;

}

.sortArea {
  padding-left: 2%;
  float:left;

}

.item-description {
    margin: 10%;
    font-size: 1.7em;
    font-family: "Lucida Grande";
    font-weight: bold;
    text-align: left;
    padding-left: 30%;
}
.item-time {
  text-align: right;
  float: right;
  color: grey;
  margin: 3px;
}

.item-distance {
  margin: 5%;
  margin-top: 0;
  padding-left: 36%;
  text-align: left;
  font-size: 1.2em;

}


.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



